import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from "../views/Login"
import Admin from "../views/Admin"

// Admin页面组件
import AddArticle from "../components/article/AddArticle";
import ArticleList from "../components/article/ArticleList";
import CategoryList from "../components/category/CategoryList";
import UserList from "../components/user/UserList";
import Index from "../components/admin/Index";
// HR页面组件
import HrKOutList from "../components/hrKOut/HrKOutList";


Vue.use(VueRouter)

const routes = [
    {
        path: '/login',
        name: 'login',
        component: Login,
    },
    {
        path: '/admin',
        name: 'admin',
        component: Admin,
        children: [
            {path: 'index', component: Index},
            {path: 'addArt', component: AddArticle},
            {path: 'artList', component: ArticleList},
            {path: 'categoryList', component: CategoryList},
            {path: 'userList', component: UserList},
            {path: 'kOutList',component: HrKOutList},
        ],
    },
]

const router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    if (to.path==='/admin/kOutList') return next()
    let token = window.localStorage.getItem('zys-token')
    if (!token) token = window.sessionStorage.getItem('zys-token')
    if (to.path === '/login' || token) return next() //必须，否则会死循环
    return next('/login') //跳转到登录界面
})

export default router
