<template>
    <div>
        <a-card class="kOutList">
            <a-row :gutter="20">
                <a-col :span="6">
                    <a-input-search
                            @search="searchKOutList"
                            v-model="Search.searchString"
                            placeholder="请输入姓名或单号"
                            enter-button
                            allowClear
                    />
                </a-col>
                <a-col :span="1">
                    <a-button type="primary" @click="loadingKOut">刷新</a-button>
                </a-col>
                <a-col :span="6">
                    <a-button type="text" v-text="LoadingTime"></a-button>
                </a-col>
            </a-row>

            <a-table
                    rowKey="OaId"
                    :columns="columns"
                    :data-source="KOutList"
                    bordered
                    size="small"
            >

                <template slot="action" slot-scope="action">
                    <div class="actionSlot">
                        <a-button type="primary">编辑</a-button>
                        <a-button type="danger" @click="deleteKOut(action.ID)">删除</a-button>
                    </div>
                </template>
            </a-table>
        </a-card>
    </div>
</template>

<script>

    const columns = [
        {
            title: 'OaId',
            dataIndex: 'OaId',
            width: 200,
            align: 'center',
        },
        {
            title: 'A01',
            dataIndex: 'A01',
            width: 150,
            align: 'center',
        },
        {
            title: 'OutReason',
            dataIndex: 'OutReason',
            align: 'center',
        },
        {
            title: 'OutBegin',
            dataIndex: 'OutBegin',
            width: 200,
            align: 'center',
        },
        {
            title: 'OutEnd',
            dataIndex: 'OutEnd',
            width: 200,
            align: 'center',
        },
        {
            title: '操作',
            align: 'center',
            fixed: 'right',
            scopedSlots: {customRender: 'action'},

        },
    ]
    export default {
        name: "HrKOutList",
        data() {
            return {
                LoadingTime: 'not data, you must loading kout list first.',
                Search: {
                    searchString: '',
                },
                KOutList: [],
                columns,
            }
        },
        methods: {
            async loadingKOut() {
                const {data: res} = await this.$http.get('/hrkout/loading')
                if (res.code !== 200) return this.LoadingTime = 'loading kout list err.'
                this.LoadingTime = `last loading: ${res.data.loadingTime}`
            },
            async searchKOutList() {
                const {data: res} = await this.$http.post('/hrkout/search', this.Search)
                if (res.code !== 200) return this.$message.warn(res.msg)
                console.log(res)
                this.KOutList = []
                this.KOutList = res.data.klist
            },
            async deleteKOut(oaid) {
                const {data: res} = await this.$http.delete(`/hrkout/${oaid}`)
                console.log(res)
            },
        }
    }
</script>

<style scoped>

    .actionSlot >>> button {
        margin: 0 5px;
    }

</style>