<template>
    <div>
        <a-card class="userListCardBody">
            <a-row :gutter="20">
                <a-col :span="6">
                    <a-input-search
                            @search="getUserList"
                            v-model="paginationOption.username"
                            placeholder="请输入用户名"
                            enter-button
                            allowClear
                    />
                </a-col>
                <a-col :span="4">
                    <a-button type="primary" @click="showCreateModal">新增</a-button>
                </a-col>
            </a-row>
            <a-table
                    rowKey="Username"
                    :columns="columns"
                    :data-source="userList"
                    :pagination="paginationOption"
                    bordered
                    size="small"
            >
                <span slot="role" slot-scope="role">{{role===1 ? '管理员':'订阅者'}}</span>
                <template slot="action" slot-scope="userScope">
                    <div class="actionSlot">
                        <a-button type="primary" @click="showEditModal(userScope)">编辑</a-button>
                        <a-button type="danger" @click="deleteUser(userScope.ID)">删除</a-button>
                    </div>
                </template>
            </a-table>
        </a-card>
        <a-modal v-model="visible" :title="modalTitle" @ok="handleOk" @cancel="handleCancel">
            <div class="modalForm">
                <a-form-model :model="modalFormData" v-bind="layout">
                    <a-form-model-item label="用户名">
                        <a-input :disabled="disabled" v-model="modalFormData.Username"/>
                    </a-form-model-item>
                    <a-form-model-item label="密码">
                        <a-input-password v-model="modalFormData.Password"/>
                    </a-form-model-item>
                    <a-form-model-item label="确认密码">
                        <a-input-password v-model="modalFormData.CheckPassword"/>
                    </a-form-model-item>
                    <a-form-model-item label="手机">
                        <a-input v-model.number="modalFormData.Phone"/>
                    </a-form-model-item>
                    <a-form-model-item label="邮箱">
                        <a-input v-model="modalFormData.Email"/>
                    </a-form-model-item>
                    <a-form-model-item label="权限">
                        <a-input :disabled="disabled" v-model.number="modalFormData.Role"/>
                    </a-form-model-item>
                </a-form-model>
            </div>
        </a-modal>
    </div>
</template>

<script>
    const columns = [
        {
            title: '序号',
            dataIndex: 'Idx',
            width: '5%',
            customRender: (text, record, index) => `${index + 1}`,
        },
        {
            title: '用户名',
            dataIndex: 'Username',
            width: 180,
            align: 'center',
        },
        {
            title: '手机',
            dataIndex: 'Phone',
            width: 180,
            align: 'center',
        },
        {
            title: '邮箱',
            dataIndex: 'Email',
            align: 'center',
        },
        {
            title: '权限',
            dataIndex: 'Role',
            align: 'center',
            scopedSlots: {customRender: 'role'},
        },
        {
            title: '操作',
            align: 'center',
            fixed: 'right',
            scopedSlots: {customRender: 'action'},

        },
    ]
    export default {
        name: "UserList",
        data() {
            return {
                userList: [],
                columns,
                paginationOption: {
                    username: '',
                    defaultCurrent: 1,
                    defaultPageSize: 10,
                    pageSizeOptions: ['10', '20', '100'],
                    total: 0,
                    showSizeChanger: true,
                    showTotal: (total) => `共${total}条`,
                    onChange: (current, pageSize) => {
                        this.paginationOption.defaultCurrent = current
                        this.paginationOption.defaultPageSize = pageSize
                        this.getUserList()
                    },
                    onShowSizeChange: (current, pageSize) => {
                        this.paginationOption.defaultCurrent = current
                        this.paginationOption.defaultPageSize = pageSize
                        this.getUserList()
                    },
                },
                visible: false,
                layout: {
                    labelCol: {span: 4},
                    wrapperCol: {span: 14},
                },
                modalTitle: '',
                modalFormData: {
                    ID: '',
                    Username: '',
                    Password: '',
                    CheckPassword: '',
                    Phone: '',
                    Email: '',
                    Role: '',
                },
                disabled: false,
            }
        },
        methods: {
            async getUserList() {
                const {data: res} = await this.$http.get('users', {
                        params: {
                            pageNum: this.paginationOption.defaultCurrent,
                            pageSize: this.paginationOption.defaultPageSize,
                            username: this.paginationOption.username,
                        }
                    }
                )
                this.userList = []
                this.paginationOption.total = 0
                if (res.code !== 200) return this.$message.warn(res.msg)
                this.userList = res.data.users
                this.paginationOption.total = res.data.total
            },
            deleteUser(id) {
                this.$confirm({
                    title: '删除确认',
                    content: '不可恢复，继续删除？',
                    onOk: async () => {
                        // 或在main.js中使用请求拦截器
                        // this.$http.defaults.headers.Authorization = 'Bearer ' + window.sessionStorage.getItem('zys-token')
                        const {data: res} = await this.$http.delete(`user/${id}`)
                        if (res.code !== 200) return this.$message.warn(res.msg)
                        this.getUserList()
                        this.$message.success('删除成功')
                    },
                    onCancel: () => {
                        this.$message.info('取消')
                    }
                })
            },
            showCreateModal() {
                this.modalTitle = '创建用户'
                this.disabled = false
                this.modalFormData = {}
                this.visible = true
            },
            showEditModal(userScope) {
                this.modalTitle = '编辑用户'
                this.disabled = true
                this.modalFormData = userScope
                this.visible = true
            },
            handleOk() {
                if (this.modalTitle === '创建用户') {
                    this.createUser()
                }
                if (this.modalTitle === '编辑用户') {
                    this.updateUser()
                }
            },
            handleCancel() {
                this.visible = false
                this.modalFormData = {}
                this.$message.info('取消')
            },
            async createUser() {
                const {data: res} = await this.$http.post('user/add', this.modalFormData)
                if (res.code !== 200) return this.$message.warn(res.msg)
                this.$message.success('用户添加成功')
                this.visible = false
            },
            async updateUser() {
                console.log(this.modalFormData)
                const {data: res} = await this.$http.put(`user/${this.modalFormData.ID}`, this.modalFormData)
                if (res.code !== 200) return this.$message.warn(res.msg)
                this.$message.success('用户更新成功')
                this.visible = false
            },
        },
        created() {
            this.getUserList()
        }
    }
</script>

<style scoped>
    .actionSlot >>> button {
        margin: 0 5px;
    }

    .actionBtn, .actionBtn >>> .ant-form-item-control {
        display: flex;
        justify-content: flex-end;
    }

    .actionBtn >>> .ant-btn {
        margin-left: 10px;
    }
</style>