import Vue from 'vue'
import {
    Button,
    Form,
    FormModel,
    Input,
    Icon,
    message,
    Checkbox,
    Layout,
    Avatar,
    Menu,
    Card,
    Table,
    Row,
    Col,
    ConfigProvider,
    Modal,
} from 'ant-design-vue'

message.config({
    top: `50px`,
    duration: 2,
    maxCount: 3,
});
Vue.prototype.$message = message
Vue.prototype.$confirm = Modal.confirm

Vue.use(Button)
Vue.use(Form)
Vue.use(FormModel)
Vue.use(Input)
Vue.use(Icon)
Vue.use(Checkbox)
Vue.use(Layout)
Vue.use(Avatar)
Vue.use(Menu)
Vue.use(Card)
Vue.use(Table)
Vue.use(Row)
Vue.use(Col)
Vue.use(ConfigProvider)
Vue.use(Modal)