<template>
    <a-layout-header class="headerBtn">
        <a-avatar v-text="username" class="userInfo" size="default"></a-avatar>
        <a-button size="default" type="danger" @click="logout">退出</a-button>
    </a-layout-header>
</template>

<script>
    export default {
        name: "Header",
        data() {
            return {
                username: 'Eddie'
            }
        },
        methods: {
            logout() {
                window.localStorage.clear('zys-token')
                window.sessionStorage.clear('zys-token')
                this.$router.push('/login')
            },
            getUsername() {
                this.username = window.localStorage.getItem('username')
            }
        },
        created() {
            this.getUsername()
        }
    }
</script>

<style scoped>
    .headerBtn {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    .userInfo {
        margin: 0 18px;
    }
</style>